



.first__container__wrapper> img{
width: auto;
height: 360px;
border-radius: 15px;
}



.first__container__wrapper{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    height: 447px;
    background-color: #657871;
    gap:60px;

}

.textblock{
    width: 423px;
    height: 278px;
}

.textblock > button {
    width: 200px;
    height: 44px;
    border-radius: 10px;
    background-color: #F4CE14;
}

.textblock > h2{
padding-bottom:20px;
}

.textblock > p{
    padding-bottom:20px;
    }

.textblock > h3 {
    padding-bottom: 30px;
}

/*Second container wrapper*/


.second__container__wrapper {
    height: 747px;
    max-width: 100%;
    background-color: #edefee;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap:50px;
  }

.card{
    width:300px;
    height: 456px;
    background-color: #657871;
    border: 1px solid black;
    border-radius: 15px;
}

.card>img{
    width:100%;
    height: 185px;
    border-radius: 10px 10px 0px 0px;

}

.card__content{
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: center;
    padding: 20px;
}

.card__content> button {
width: 120px;
height: 35px;
background-color: #F4CE14;
border-radius: 15px;
border: 1px solid black;
}

.card__title{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.card__title > h3{
line-height: 0px;
}

.card__title > p{
    word-wrap: break-word;
    line-height: 0px;
    }

.content__wrapper__two{
width: 60%;
height: 456px;
display: flex;
align-items: center;
justify-content: space-evenly;

    }

.second__container__title{
    width: 100%;
    height: 97px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.second__container__title > h3 {
    font-size: 32px;
}

.second__container__title > button{
width: 200px;
height: 44px;
background: #F4CE14;
border-radius: 10px;
}


/*Third container wrapper*/

.third__container__wrapper{
    max-width: 100%;
    height: 606px;
    background-color: #d2d4d3;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.content__wrapper__three{
    max-width: 860px;
    height: 329px;
   display: flex;
   justify-content: center;
   align-items: center;
}

.testimonials_card_wrapper{
    display: flex;
    width: 70%;
    justify-content: space-evenly;
}

.testimonials__card{
width: 200px;
height: 220px;
background: #677D75;
border-radius: 15px;
padding: 20px;
border: 1px solid black;
display: flex;
flex-direction: column;
justify-content: space-evenly;
}

.card__name{
    display: flex;
    gap: 20px;
}

.card__name>img{
width: 88px;
height: 82px;
border: 1px solid #000000;
}

.third__container__wrapper>h1{
    font-size: 32px;
    line-height: 80px;
    padding-bottom: 20px;
}

.stars{
    display: flex;
    align-items: center;
    gap: 20px;
}

.star__stack{
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
}

.stars>h3{
  font-size: 18px;
}

.my__custom__star{
    color:#F4CE14
}

/*Forth container wrapper*/

.forth__container__wrapper{
    height: 606px;
    background-color:#657871;
    display: flex;
    align-items: center;
    justify-content: center;
}

.forth__card__wrapper{
width: 100%;
height: 409px;
display: flex;
justify-content: center;
gap:300px;
padding: 10px;
}

.forth__container__textblock{
width: 314px;
height: 204px;
}

.forth__container__textblock>h1{
    padding-bottom: 5px;
}

.forth__container__textblock>h3{
    padding-bottom: 20px;
}


.forth__container__pictures__absolute {
width: 450px;
height: 409px;
}

.forth__container__pictures__absolute > img{
width: 280px;
height: 307px;
background-color: grey;
border-radius: 8px;
}

.forth__container__pictures__absolute > .img-1{
position: relative;
width: auto;
right:-50px;
top: 10px;
}


.forth__container__pictures__absolute > .img-2{
   position: relative;
   right: 70px;
   bottom: 220px;
}

button {
    transition: all 0.5s ease;
  }
  
  button:hover {
    background-color: #ffd500;
    transform: scale(1.1);
  }

  img {
    transition: transform 0.3s ease;
  }

  img:hover {
    transform: scale(1.01);
  }


